<template>
	<div>
		<ul class="list"  v-for="item in list" >
			<li class="time">{{item.time}}&nbsp &nbsp{{item.address}}</li>
			<li class="addres">{{item.content}}</li>
		</ul>
	</div>
	
</template>

<script>
	export default {
		name:'search',
		data(){
			return{
				list:null
			}
		},
		methods:{
			data(data){
				this.list = data
			}
		},
		mounted() {
		  this.$bus.$on('data',this.data)
		}
	}
</script>

<style scoped>
	
	
	.list {
		width: 100%;
		margin-top: 10%;
		
	}
	
</style>